import React from 'react';
import { Link } from 'react-router-dom';

const NavBar = () => (
  //Empty component
  <nav>
    <ul>
      <li>
        <Link to='/'>Home</Link>
      </li>
      <li>
        <Link to='/about'>About Us</Link>
      </li>
      <li>
        <Link to='/article-list'>Article List</Link>
      </li>
    </ul>
  </nav>
);
export default NavBar;
